input {
  padding: 0px 10px;
  border-radius: 6px;
  outline: none;
  border: 2px solid #57606a;
  background-color: inherit;
  font-size: 14px;
  box-sizing: border-box;
}
input::-webkit-input-placeholder {
  color: #c8c9cb;
}
input,
input:focus,
input::placeholder {
  color: #c8c9cb;
}

.btn {
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
.btn-warning {
  background-color: #ffff35;
  padding: 0px 10px;
  color: #24292f;
  font-size: 16px;
}
button[disabled] {
  cursor: not-allowed;
}
.btn-warning:disabled {
  background-color: #e3e35a;
}
.btn-warning:disabled,
.btn-warning:disabled svg {
  color: #c8c9cb;
  fill: #c8c9cb;
}
.btn-warning svg {
  margin-right: 4px;
}

.search-user-form {
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.search-user-form input {
  min-height: 40px;
  width: 50%;
  margin-right: 4%;
}
.search-user-form .btn-warning {
  height: 40px;
  min-width: 20%;
}
@media only screen and (max-width: 480px) {
  .search-user-form {
    flex-direction: column;
  }
  .search-user-form input,
  .search-user-form .btn-warning {
    width: 100%;
    margin-right: 0%;
  }
  .search-user-form input {
    margin-right: 0%;
    margin-bottom: 8%;
  }
}

.searched-users-list ul,
.searched-users-list p {
  border-radius: 0px 0px 6px 6px;
  background-color: #f3f2f2;
}
.searched-users-list p {
  width: 50%;
  padding: 10px;
  box-sizing: border-box;
}
.searched-users-list ul {
  width: 50%;
  overflow: auto;
}
.searched-users-list ul li {
  padding: 10px;
  border-bottom: 1px solid #57606a;
  cursor: pointer;
  box-sizing: border-box;
}
.searched-users-list ul li:last-child {
  border-bottom: none;
}
.searched-users-list ul li:hover {
  background-color: #cfcece;
}
@media only screen and (max-width: 480px) {
  .searched-users-list {
    width: 77%;
    position: absolute;
    top: 23%;
    z-index: 999;
  }
  .searched-users-list p {
    width: 100%;
  }
  .searched-users-list ul {
    width: 100%;
  }
}

.not-found {
  flex-direction: column;
  width: 50%;
  background: #c8c9cb;
  border-radius: 6px;
  color: #24292f;
  padding: 50px;
  box-sizing: border-box;
}
.not-found__title {
  margin-top: 8%;
}
.not-found__title h3 {
  font-size: 20px;
  font-weight: 500;
}

.user-id-card {
  margin: 3% auto;
}
.user-id-card .not-found,
.user-id-card__content {
  width: 74%;
}
.user-id-card__content {
  background: #c8c9cb;
  border-radius: 6px;
  padding: 20px 20px 35px;
  box-sizing: border-box;
  color: #24292f;
}
.user-id-card__content__header {
  padding: 10px 0px;
  box-sizing: border-box;
}
.user-id-card__content__header h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
}
.user-id-card__content__img {
  padding: 15px 0px;
  box-sizing: border-box;
}
.user-id-card__content__img img {
  width: 30%;
}
.user-id-card__content__details {
  width: 75%;
  margin: 0 auto;
}
.user-id-card__content__details p {
  padding: 5px 0px;
}
.user-id-card__field {
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
}
@media only screen and (max-width: 480px) {
  .user-id-card,
  .user-id-card__content,
  .user-id-card__content__details,
  .user-id-card .not-found {
    width: 100%;
  }
  .user-id-card__content__header h2 {
    font-size: 20px;
  }
  .user-id-card__field {
    font-size: 16px;
  }
  .user-id-card__content {
    padding-bottom: 25px;
  }
  .user-id-card .not-found {
    padding: 40px;
  }
}

.header {
  padding: 30px 0px;
  box-sizing: border-box;
}
.header h1 {
  font-size: 26px;
  color: #c8c9cb;
  font-weight: 500;
}

.loading {
  height: 100%;
}
.loading .lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.loading .lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #c8c9cb;
  border-color: #c8c9cb transparent #c8c9cb transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.container {
  width: 90%;
  margin: 0 auto;
  flex-direction: column;
}

